@import "../../01-atoms/02-typography/01-fonts.scss";
@import "../../00-protons/00-breakpoints";
@import "../../01-atoms/01-colors/01-colors";
@import "../../01-atoms/03-buttons/01-buttons";
@import "../../00-protons/02-mixins";

$site-max-width: 1040px;
$z-index-header:  7000;
$z-index-overlay: 8000;
$z-index-nav:     9000;
$hit-area: 4.4rem;

.sfgov-main-navigation {
  ul {
    li {
      display: inline-block;
      margin-right: 40px;
      a {
        @include source-sans-pro;
        font-size: 15px;
        // todo: add color to colors atom.
        color: #323a45;
        line-height: 19px;
        font-weight: bold;
        text-decoration: none;
      }
    }
  }
  .sfgov-mobile-menu & {
    ul {
      li {
        display: block;
      }
    }
  }
}

.sfgov-menu-btn {
  @include button-unstyled;
  background-color: $color-primary;
  color: $color-white;
  display: inline;
  float: right;
  font-size: 1.3rem;
  height: 4rem;
  line-height: 4rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  width: 5.8rem;
  min-height: 60px;

  @media all and (min-width: $narrow-screen) {
    display: none;
    min-height: initial;
  }

  &:hover {
    color: $color-white;
    background-color: $color-primary-darker;
  }

  &:visited {
    color: $color-white;
  }
}

.sfgov-overlay {
  position: fixed;
  background: $color-black;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  visibility: hidden;
  z-index: 8000;

  &.is-visible {
    opacity: 0.1;
    visibility: visible;
  }
}

@mixin nav-border {
  border-bottom: 0.4rem solid $color-primary;
  padding-bottom: 0.6rem;
}

@mixin nav-border-thick {
  border-bottom: 0.7rem solid $color-primary;
  padding-bottom: 0.9rem;
}

.sfgov-navbar {
  border-bottom: 1px solid $color-gray-light;
  height: 4rem;

  @media all and (min-width: $narrow-screen + 1) {
    border-bottom: none;
    display: inline-block;
    height: 7.8rem;
  }
}

.sfgov-nav-link {
  @include font-smoothing;

  &:hover {
    span {
      @media all and (min-width: $narrow-screen + 1) {
        @include nav-border;
      }
    }
  }

  &.sfgov-accordion-button {
    span {
      @media all and (min-width: $narrow-screen + 1) {
        margin-right: 0;
        padding-right: 1.5rem;
      }
    }
  }
}

.sfgov-nav {
  $sliding-panel-width: -26rem;
  @keyframes slidein-right {
    from {
      transform: translateX($sliding-panel-width);
    }

    to {
      transform: translateX(0);
    }
  }
  position: fixed;
  left: 0;
  top: 0;
  right: auto;
  bottom: 0;
  background: $color-white;
  border-left: 1px solid $color-gray-light;
  border-right: 0;
  display: none;
  flex-direction: column;
  overflow-y: auto;
  padding: 2rem;
  width: $sliding-panel-width;
  z-index: $z-index-nav;

  @media all and (min-width: $narrow-screen + 1) {
    padding: 0;
    border-left: none;
    display: block;
    float: right;
    overflow-y: visible;
    position: relative;
    transform: translateX(0);
    width: auto;
  }

  &.is-visible {
    animation: slidein-right 0.3s ease-in-out;
    display: flex;
  }

  nav {
    margin-top: 6rem; // XXX magic number
    min-height: 100%;

    @media all and (min-width: $narrow-screen + 1) {
      margin-top: 0;
    }
  }

  .sfgov-current {
    border-left: 4px solid $color-primary;
    color: $color-primary;
    font-weight: $font-bold;
    padding-left: 1.4rem;

    @media all and (min-width: $narrow-screen + 1) {
      color: $color-base;
    }
  }

  .sfgov-button {
    width: 100%;
  }

  .sfgov-search {
    @media all and (min-width: $narrow-screen + 1) {
      margin-left: 1.5rem; // XXX magic number
      top: 4px;
    }
  }
}

.sfgov-nav-close {
  @include button-unstyled;
  margin:-1.2rem -1.5rem 1.5rem auto;
  color: currentColor;
  float: right;
  height: $hit-area;
  text-align: center;
  width: $hit-area;

  &:hover {
    color: currentColor;
  }

  @media all and (min-width: $narrow-screen + 1) {
    display: none;
  }

  img {
    width: 1.3rem;
  }

  + * {
    clear: both;
  }
}

.sfgov-mobile_nav-active {
  overflow: hidden;
}

.sfgov-mobile_nav-container {
  @media all and (max-width: $narrow-screen) {
    .sfgov-main-navigation {
      ul.menu {
        padding: 0;
        margin: 0;
        li {
          display: block;
          border-bottom: 1px solid #323a44;
          margin-right: 0;
          min-width: 231px;
          padding-left: 20px;
          padding-bottom: 8px;
          &:last-child {
            border-bottom: 0;
          }
          a {
            @include source-sans-pro;
            font-size: 17px;
            font-weight: 400;
            line-height: 22px;
          }
        }
      }
    }
  }
}
